import React from 'react'
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js'
import { Doughnut } from 'react-chartjs-2'
import { toyService } from '../services/toyService.js'

ChartJS.register(ArcElement, Tooltip, Legend)

export const DashBoard = () => {
  const labels = toyService.getLabels()
  const inventory = toyService.getInventory()

  if (!labels || !inventory) return <div>Loading...</div>

  const data = {
    labels: labels,
    datasets: [
      {
        label: 'Number of Items',
        data: inventory,
        backgroundColor: [
          'rgba(255, 99, 132, 0.2)',
          'rgba(54, 162, 235, 0.2)',
          'rgba(255, 206, 86, 0.2)',
          'rgba(75, 192, 192, 0.2)',
          'rgba(153, 102, 255, 0.2)',
          'rgba(255, 159, 64, 0.2)',
          'rgba(255, 0, 0, 0.2)',
          'rgba(0, 255, 0, 0.2)',
          'rgba(0, 0, 255, 0.2)',
          'rgba(255, 255, 0, 0.2)',
          'rgba(255, 0, 255, 0.2)',
          'rgba(0, 255, 255, 0.2)',
          'rgba(128, 0, 0, 0.2)',
          'rgba(0, 128, 0, 0.2)',
          'rgba(0, 0, 128, 0.2)',
          'rgba(128, 128, 0, 0.2)',
          'rgba(128, 0, 128, 0.2)',
          'rgba(0, 128, 128, 0.2)',
          'rgba(255, 128, 0, 0.2)',
          'rgba(255, 0, 128, 0.2)',
          'rgba(128, 255, 0, 0.2)',
          'rgba(0, 255, 128, 0.2)',
          'rgba(128, 0, 255, 0.2)',
          'rgba(0, 128, 255, 0.2)',
          'rgba(255, 128, 128, 0.2)',
          'rgba(128, 255, 128, 0.2)',
          'rgba(128, 128, 255, 0.2)',
          'rgba(192, 192, 192, 0.2)',
          'rgba(128, 128, 128, 0.2)',
          'rgba(64, 64, 64, 0.2)',
          'rgba(255, 255, 255, 0.2)',
        ],
        borderColor: [
          'rgba(255, 99, 132, 1)',
          'rgba(54, 162, 235, 1)',
          'rgba(255, 206, 86, 1)',
          'rgba(75, 192, 192, 1)',
          'rgba(153, 102, 255, 1)',
          'rgba(255, 159, 64, 1)',
          'rgba(255, 0, 0, 1)',
          'rgba(0, 255, 0, 1)',
          'rgba(0, 0, 255, 1)',
          'rgba(255, 255, 0, 1)',
          'rgba(255, 0, 255, 1)',
          'rgba(0, 255, 255, 1)',
          'rgba(128, 0, 0, 1)',
          'rgba(0, 128, 0, 1)',
          'rgba(0, 0, 128, 1)',
          'rgba(128, 128, 0, 1)',
          'rgba(128, 0, 128, 1)',
          'rgba(0, 128, 128, 1)',
          'rgba(255, 128, 0, 1)',
          'rgba(255, 0, 128, 1)',
          'rgba(128, 255, 0, 1)',
          'rgba(0, 255, 128, 1)',
          'rgba(128, 0, 255, 1)',
          'rgba(0, 128, 255, 1)',
          'rgba(255, 128, 128, 1)',
          'rgba(128, 255, 128, 1)',
          'rgba(128, 128, 255, 1)',
          'rgba(192, 192, 192, 1)',
          'rgba(128, 128, 128, 1)',
          'rgba(64, 64, 64, 1)',
          'rgba(255, 255, 255, 1)',
        ],
        borderWidth: 1,
      },
    ],
  }
  return (
    <>
      <div className="canvas-wrapper">
        <h1>Inventory</h1>
        <Doughnut data={data} />
      </div>
    </>
  )
}
